import React, {useState, useEffect} from 'react';
import axios from 'axios';
import '../style/style.scss';
import Alert from '../hooks/alert';
import QueryString from 'querystring';

//
const Login = () => {

    const [joinModal, setJoinModal] = useState(false);

    return(
        <div className='window'>
            <div className='loginWindow'>
                <h1>LOGIN</h1>
                <form id='LoginForm' method='post'>
                    <input type='text' name='id' placeholder='ID (Email)' />
                    <input type='text' name='pw' placeholder='Password'/>
                    <label>
                        <input type='checkbox' id='remember-id'/>   
                    </label>
                </form>
                <button className='loginBtn' type='submit' style={{float:'left'}}></button>
                <button className='loginBtn' type='button' style={{float:'right'}} onClick={()=>{setJoinModal(true)}}></button>
                {joinModal === true? <Join close={setJoinModal} /> : null}
            </div>
        </div>  
    );
};

//
const Join = (props) => {

    const {close} = props; // 
    const [alertModal, setAlertModal] = useState(false); //  .
    const [alertValue, setAlertValue] = useState('');

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [pw, setPw] = useState(''); //
    const [pwCheck, setPwCheck] = useState('');

    // 
    const nameRegEx = /^[-]+$/;
    const emailRegEx = /^[A-Za-z0-9]([-_.]?[A-Za-z0-9])*@[A-Za-z0-9]([-_.]?[A-Za-z0-9])*\.[A-Za-z]{2,3}$/;
	const pwRegEx = /^[A-Za-z0-9]{8,20}$/;

    const [formCheck, setFormCheck] = useState({
        name:false,
        email: false, 
        pw: false,
        pwCheck: false,
    });

    const JoinAccess = () => { //     
        
        //, , pw  
        setFormCheck({...formCheck, name:nameRegEx.test(name), email:emailRegEx.test(email), pw:pwRegEx.test(pw), pwCheck:pw===pwCheck}); 
       
        if(formCheck.name && formCheck.email && formCheck.pw && formCheck.pwCheck){
            axios({
                method:'post', //method:   
                url:'https://tjddus0630.cafe24.com/sy_memo/ajax/ajax_join_update.php',
                data:QueryString.stringify({name:name, email:email, pw:pw}),
            })
            .then(res => res.data)
            .then(data => {
                if(data.result){
                    setAlertModal(' .');
                }
                else{
                    setAlertModal('   .');
                }
            }) // 
        }else if(!formCheck.name){
            setAlertModal(true);
            setAlertValue('   .');
        }else if(!formCheck.email){
            setAlertModal(true);
            setAlertValue('   .');
        }else if(!formCheck.pw){
            setAlertModal(true);
            setAlertValue('   .');
        }else{
            setAlertModal(true);
            setAlertValue('   .');
        }
        
    }

    return(
        <div className='window'>
            <div className='joinWindow'>
                <h1></h1>
                <form method='post' id='LoginForm' >
                    <input type='text' name='name' placeholder='' value={name} onChange={(e)=>{setName(e.target.value)}}/>
                    <input type='text' name='email' placeholder='ID (Email)' value={email} onChange={(e)=>{setEmail(e.target.value)}}/>
                    <input type='password' name='pw' placeholder='' value={pw} onChange={(e)=>{setPw(e.target.value)}}/>
                    <p> *   ,   8~20 .</p>
                    <input type='password' name='pwcheck' placeholder=' ' onChange={(e)=>{setPwCheck(e.target.value)}} />
                </form>
                <button type='submit' className='joinBtn' style={{float:'left'}} onClick={()=>JoinAccess()}></button>
                <button className='joinBtn' style={{float:'right'}} onClick={()=>{close(false)}}></button>
                {alertModal === true? <Alert close={setAlertModal} value={alertValue}/> : null}
            </div>
        </div>
    );
};

export default Login;